const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const classs = ["SMS", "LMS", "Video Conferencing", "Demo"];
const data = [
  
  {
    FirstName:"",
    LastName:"",
    Gender:"",
    DOB:"",
    ClassName:"",
    SectionName:"",
    AdmissionNo:"",
    ParentFirstName:"",
    ParentLastName:"",
    ParentEmail:"",
    ParentMobileNumber:"",
    ParentGender:"",
    username:""
    
}
] 
const UserManagement = (props) => {
  
  // set form values
 let school_id= props.match.params.school_id;
  const classes = useStyles();
  const [userID, setUSerID] = useState("");
  const [school_name, setSchool_name] = useState("");
  const [address, setAddress] = useState("");
  const [pin, setPin] = useState("");
  const [website, setWebsite] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [selectedFile, setSelectedFile] = useState("");
  const [academics_from, setAcademics_from] = useState("");
  const [academics_to, setacademics_to] = useState("");
  const [license_start, setLicense_start] = useState("");
  const [license_end, setLicense_end] = useState("");
  const [total_users, setTotal_users] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [state_name, setState_name] = useState([]);
  const [country_code, setCountry_code] = useState();
  const [city, setCity] = useState([]);
  const [stateName, setStateName] = useState();
  const [cityName, setCityName] = useState();
  const [visible, setVisible] = useState(false);
  const [classNames, setClassNames] = React.useState([]);
  const [countrylist, setCountryList] = useState([]);
  const [chainSchools, setChainSchools] = useState([]);
  const [chainSchoolId, setCahinSchoolId] = useState("");
  const [logoName, setLogoName] = useState("");

  // Defining Errors
  const [userIDErr, setUserIDErr] = useState({});
  const [schoolNameErr, setSchoolNameErr] = useState({});
  const [pinErr, setPinErr] = useState({});
  const [stateErr, setStateErr] = useState({});
  const [cityErr, setCityErr] = useState({});
  const [countryErr, setCountryErr] = useState({});
  const [addressErr, setAddressErr] = useState({});
  const [webErr, setWebErr] = useState({});
  const [emailErr, setEmailErr] = useState({});
  const [mobileErr, setMobileErr] = useState({});
  const [passwordErr, setPasswordErr] = useState({});
  const [password2Err, setPassword2Err] = useState({});
  const [calStartErr, setCalStartErr] = useState({});
  const [calEndErr, setCalEndErr] = useState({});
  const [academicStartErr, setacAdemicStartErr] = useState({});
  const [academicEndErr, setAcademicEndErr] = useState({});
  const [totalErr, setTotalErr] = useState("");
  const [uploadingmessage, setUploadingmessage] = useState("");


  const uploadexcel = async() =>{
  
   // console.log(items);
  const itemss=items;
  
  // for(var i=0;i<itemss.length;i++){
  //   itemss[i].school_id="123456"
  // }
  console.log(itemss);
  for(var i=0;i<itemss.length; i++)
  {
    var letters = /^[A-Za-z]+$/;
    if(itemss[i].FirstName.match(letters) && (itemss[i].FirstName) && (itemss[i].ClassName)  && (itemss[i].SectionName) && (itemss[i].ParentFirstName) && itemss[i].ParentFirstName.match(letters) && !isNaN(itemss[i].ParentMobileNumber) && (itemss[i].ParentMobileNumber).length==10 )
    {
    
     
      itemss[i].username=itemss[i].FirstName.substring(0,4).concat(itemss[i].ParentMobileNumber.slice(-5));

    }
    else{
      itemss[i].error=[];
      if(!itemss[i].FirstName.match(letters)  )
      {
        itemss[i].error.push("error in first name:first name should not have spaces or numbers")
      }
        if(!(itemss[i].FirstName))
      {
        itemss[i].error.push("error:first name cannot be left blank")
      }
     if( !(itemss[i].ClassName) )
  {
    itemss[i].error.push("error:Class name cannot be left blank")
    
  }
  if(  (!itemss[i].SectionName))
  {
    itemss[i].error.push("error:Section name cannot be left blank")
   
  }
  if(  !(itemss[i].ParentFirstName) || !itemss[i].ParentFirstName.match(letters))
  {
    itemss[i].error.push("error:Parent first name cannot be left blank and cannot contain spaces or numbers")
   
  }
   if( isNaN(itemss[i].ParentMobileNumber)  )
  {
    itemss[i].error.push("error:Parent mobile number should be of 10 digits and cannot contain albhabets")
   
  }
   if( (itemss[i].ParentMobileNumber).length!=10){
    itemss[i].error.push("error:Parent mobile number should be of 10 digits and cannot contain albhabets")
   
  }
   
   itemss[i].FirstName="";
   itemss[i].LastName="";
   itemss[i].AdmissionNo="";
   itemss[i].ParentEmail="";
   itemss[i].ParentLastName="";
   itemss[i].ParentMobileNumber="";
   itemss[i].ParentGender="";
   itemss[i].Gender="";
   itemss[i].DOB="";
   itemss[i].ParentFirstName="";
   itemss[i].ClassName="";
   itemss[i].SectionName="";
   itemss[i].username="";
    }

//itemss[i].username=itemss[i].FirstName.substring(0,4).concat(itemss[i].ParentMobileNumber.slice(-5));
  }
  setItems(itemss);
      return await EDU_AXIOS.post(`${process.env.REACT_APP_API_PATH}/auth/create-student/${school_id}`,itemss) .then(res => {
        console.log("after !!!!")
        console.log(res.data);
        if(res.data.message!="student has been created"){
        setTotalErr(res.data.error);
        alert(res.data.error);
        }
        else{
          setUploadingmessage("student has been created")
        }
       
      }).catch(err => {
        console.log(err)
      });
  }
  // to get country list from server
  useEffect(() => {
    EDU_AXIOS.get(process.env.REACT_APP_API_PATH + "/auth/get-countries")
      .then((response) => {
        let countryArray = response.data.data;
        setCountryList(countryArray);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  // to get chain school list from server
  useEffect(() => {
    EDU_AXIOS.get(
      process.env.REACT_APP_API_PATH + "/auth/get-chain-school-list"
    )
      .then((response) => {
        setChainSchools(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  // set country code before calling state list api
  const selectState = (e) => {
    var countryCode = e.target.value;
    setCountry_code(countryCode);
  };

  // get states by country code
  useEffect(() => {
    // check country code is set or not
    if (country_code) {
      EDU_AXIOS.get(
        process.env.REACT_APP_API_PATH + `/auth/get-states/${country_code}`
      )
        .then((resp) => {
          let stateArray = resp.data.data;
          setState_name(stateArray);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [country_code]);

  // get cities of a state
  useEffect(() => {
    // check coundry code and state name is set or not
    if (stateName) {
      EDU_AXIOS.get(
        process.env.REACT_APP_API_PATH +
          `/auth/get-cities/${country_code}/${stateName}`
      )
        //EDU_AXIOS.get(cityURL)
        .then((respo) => {
          let cityArray = respo.data.data;

          setCity(cityArray);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [stateName]);

  const handleChange = (event) => {
    setClassNames(event.target.value);
  };

  const [inputList, setInputList] = useState([
    { class_name: "", total_student: "" },
  ]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
    // console.log(inputList,"B");
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([...inputList, { class_name: "", total_student: "" }]);
  };

  // get a file to upload
  const handleFileInput = (e) => {
    setLogoName(e.target.files[0].name);
    setSelectedFile(e.target.files[0]);
  };
  // upload selected file to s3 bucket
  const handleUpload = async (file) => {
    uploadFile(file, config)
      .then((data) => console.log(data, "Logo uploaded successfully"))
      .catch((err) => console.error(err, ""));
  };

  // const [validated, setValidated] = useState(false);
  const onSubmit = async (e) => {
    e.preventDefault();
    const isValid = await formValidation();
    // check form is valid or not
    if (isValid) {
      return await EDU_AXIOS.post(
        process.env.REACT_APP_API_PATH + "/auth/create-normal-school",
        {
          name: school_name,
          username: userID,
          password: password,
          email: email,
          mobile: mobile,
          address: address,
          pin_code: pin,
          state: stateName,
          city: cityName,
          website: website,
          acadmics_from: academics_from,
          acadmics_to: academics_to,
          license_start: license_start,
          license_end: license_end,
          classes: inputList,
          total_users: total_users,
          license_type: classNames,
          logo: logoName,
          chain_school_id: chainSchoolId,
        }
      )
        .then(async (res) => {
          e.target.reset();
          alert(res.data.message);
        })
        .catch((err) => {
          alert(err);
        });
    }
  };

  const formValidation = async () => {
    const userIDErr = {};
    const schoolNameErr = {};
    const pinErr = {};
    const webErr = {};
    const emailErr = {};
    const mobileErr = {};
    const passwordErr = {};
    const password2Err = {};
    const stateErr = {};
    const cityErr = {};
    const countryErr = {};
    const addressErr = {};
    const calStartErr = {};
    const calEndErr = {};
    const academicStartErr = {};
    const academicEndErr = {};
    const totalErr = {};

    let isValid = true;
    // Validations for different fields

    if (!userID.trim()) {
      userIDErr.UserIDrequired = "Required";
    } else if (/[^a-zA-Z0-9_.]/.test(userID) || userID.trim().length < 6) {
      userIDErr.UserIDinvalid =
        "UserID is Invalid (@ , # not supported) / < 6 Digits";
      isValid = false;
    }

    if (!license_start.trim()) {
      calStartErr.calShort = " Required";
      isValid = false;
    }
    if (!license_end.trim()) {
      calEndErr.cal = " Required";
      isValid = false;
    }
    if (!academics_from.trim()) {
      academicStartErr.start = " Required";
      isValid = false;
    }
    if (!academics_to.trim()) {
      academicEndErr.end = " Required";
      isValid = false;
    }
    if (!total_users.trim()) {
      totalErr.total = " Required";
      isValid = false;
    }

    if (!school_name.trim()) {
      schoolNameErr.schoolNameShort = " Required";
      isValid = false;
    }

    if (!pin.trim()) {
      pinErr.pinreq = " Required";
      isValid = false;
    } else if (!/[123456789][0-9]{5}/.test(pin) || pin.trim().length > 6) {
      pinErr.pinshort = "PIN should be 6 Digits only / PIN Can't Start with 0";
      isValid = false;
    }
    if (!address.trim()) {
      addressErr.addressShort = " Required";
      isValid = false;
    }

    if (!email.trim()) {
      emailErr.emailrequired = "Required";
      isValid = false;
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      emailErr.emailinvalid = "Email address is invalid";
      isValid = false;
    }

    if (!mobile.trim()) {
      mobileErr.mobreq = " Required";
      isValid = false;
    } else if (mobile.trim().length > 10 || !/[6789][0-9]{9}/.test(mobile)) {
      mobileErr.mobshort = "Mobile number must be Valid & of 10 Digits Only";
      isValid = false;
    }

    if (!password.trim()) {
      passwordErr.passreq = " Required";
      isValid = false;
    } else if (password.trim().length < 6 || password.trim().length > 20) {
      passwordErr.passshort = "Invalid / < 6 Digits";
      isValid = false;
    }

    if (!password2.trim()) {
      passwordErr.pass2req = " ";
      isValid = false;
    } else if (password2 !== password) {
      password2Err.pass2short = "Doesn't Match ";
      isValid = false;
    }
    // setting errors in validation
    setUserIDErr(userIDErr);
    setSchoolNameErr(schoolNameErr);
    setPinErr(pinErr);
    setWebErr(webErr);
    setEmailErr(emailErr);
    setMobileErr(mobileErr);
    setPasswordErr(passwordErr);
    setPassword2Err(password2Err);
    setCityErr(cityErr);
    setStateErr(stateErr);
    setCountryErr(countryErr);
    setAddressErr(addressErr);
    setCalStartErr(calStartErr);
    setCalEndErr(calEndErr);
    setacAdemicStartErr(academicStartErr);
    setAcademicEndErr(academicEndErr);
    setTotalErr(totalErr);
    return isValid;
  };
  const [items, setItems] = useState([]);
  const fileName = 'download'  
const exportType = 'xls'
 const  ExportToExcel = () => {  
    exportFromJSON({ data, fileName, exportType })  
  } 
  const readExcel = (file) => {
    const yourToken=JSON.parse(localStorage.getItem("user")).token;
    const promise = new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsArrayBuffer(file);

      fileReader.onload = (e) => {
        const bufferArray = e.target.result;

        const wb = XLSX.read(bufferArray, { type: 'binary', cellDates: true, dateNF: 'mm/dd/yyyy;' });

        const wsname = wb.SheetNames[1];

        const ws = wb.Sheets[wsname];

        const data = XLSX.utils.sheet_to_json(ws,{
          raw: false,
        });

        resolve(data);
      };

      fileReader.onerror = (error) => {
        reject(error);
      };
    });

    promise.then(async(d) => {
      setItems(d);
      // console.log(d);
      console.log(yourToken);
    //   let header= {
    //     headers: {'Authorization': "Bearer " + yourToken}
    // };
    
    //   return await Axios.post(process.env.REACT_APP_API_PATH + "/auth/create-student/",d,header) .then(res => {
    //     console.log(res)
    //   }).catch(err => {
    //     console.log(err)
    //   });;
  // console.log(request);
    });
  };
  return (
    //  School Creation Form
   
    <div>
     
      <form>
      <input
        type="file"
        onChange={(e) => {
          const file = e.target.files[0];
          readExcel(file);
        }}
      />
      <button type="button" className="btn btn-success" onClick={uploadexcel}>Upload</button> &nbsp;
      {/* <button type="button" onClick={ExportToExcel}>Download standard template</button>   */}
      <a href="/files/StandardStudentTemplate.xlsx" className="btn btn-primary" download>Download standard template</a>
</form>
      <table class="table container">
        <thead>
         
              <tr>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Gender</th>
              <th scope="col">DOB</th>
              <th scope="col">Class Name</th>
              <th scope="col">Section Name</th>
              <th scope="col">Admission No</th>
              <th scope="col">Parent FirstName</th>
              <th scope="col">Parent LastName</th>
              <th scope="col">Parent Email</th>
              <th scope="col">Parent Gender</th>
              <th scope="col">Parent MobileNumber</th>
              <th scope="col">username</th>
            </tr>
            
         

        </thead>
        <tbody> 
          {
          uploadingmessage!=""? items.map((d) => (
        
 d.FirstName!='' && d.LastName!='' && d.AdmissionNo!=''&& d.ParentEmail!='' && d.ParentFirstName!='' && d.ParentLastName!='' && d.ParentGender!='' && d.ParentMobileNumber!=''  ? 
            <tr key={d.FirstName}>
              <th>{d.FirstName}</th>
              <th>{d.LastName}</th>
              <th>{d.Gender}</th>
              <th>{d.DOB}</th>
              <th>{d.ClassName}</th>
              <th>{d.SectionName}</th>
              <th>{d.AdmissionNo}</th>
              <th>{d.ParentFirstName}</th>
              <th>{d.ParentLastName}</th>
              <th>{d.ParentEmail}</th>
              <th>{d.ParentGender}</th>
              <th>{d.ParentMobileNumber}</th>
              <th>{d.username}</th>
              {/* <td>{d.yearofbatch}</td>
              <td>{d.college_id}</td>
               <td>{d.skills}</td> */}
             </tr>
             :<tr style={{border:"3px solid red"}} className="danger"><td colSpan="13" >{d.error.map((err)=>(
            <p>{err}</p>
             ))}</td></tr>
           )) :''
           }
         </tbody>
      </table>
    </div>
  );
};

export default UserManagement;
